body {
  text-align: left;
  min-height: 100vh;
  color: #fff;
  background: teal;
  font-family: Montserrat, "sans-serif";
  font-size: 1.2rem;
  position: static;
  top: 0;
  left: 0;
  transform: translate(0%);
}

form {
  justify-content: flex-start;
  align-items: flex-end;
  display: flex;
}

label, button {
  text-align: center;
  border-radius: 50px;
  flex-direction: column;
  padding: 8px;
  display: inline-flex;
}

label {
  margin-right: 16px;
}

p {
  display: inline-flex;
}

button {
  color: #fff;
  background-image: linear-gradient(90deg, #0f2 0, #fbff00 100%);
  border: 4px solid #000;
  justify-content: center;
  font-family: Montserrat, "sans-serif";
  font-size: 1.2rem;
}

